@import url(https://fonts.googleapis.com/css2?family=Mate+SC&family=Raleway:wght@500&display=swap);
body{background:#f0f7ee;color:#3a3e40}body a{color:#2c9056}button.linkish{border:none;background:none;cursor:pointer;font-family:"Raleway",sans-serif;font-size:1em;outline:none;border-right:1px dashed #3a3e40;float:right;padding:0px 7px}button.linkish:last-of-type{border-left:1px dashed #3a3e40}button.linkish.sel{background:#2c9056;color:#f0f7ee}#myResume{display:grid;width:100vw;height:100vh;grid-template-columns:1fr;grid-template-rows:repeat(4, 1fr);grid-column-gap:10px;grid-row-gap:10px;font-family:"Raleway",sans-serif;margin:0 auto;max-width:800px;padding-bottom:40px}#myResume header{display:grid;grid-template-rows:1fr;grid-template-columns:110px 1fr 1fr;padding-bottom:10px}#myResume header h2{cursor:pointer;font-family:"Mate SC",sans-serif;font-size:3em;font-weight:normal;margin:32px 0}#myResume header .info{background-color:#232fd1;border-radius:25px;border:none;color:#fff;font-family:serif;font-size:22px;font-weight:bold;padding:5px 15px;margin:10px 10px}#myResume header .face{border:1px solid #3a3e40;border-radius:50px;margin:25px 20px 10px 0;width:5em}#myResume header .about{border:none;background:none;font-family:"Mate SC",sans-serif;font-size:20px;-webkit-user-select:none;-ms-user-select:none;user-select:none}#myResume #specialties{margin:5px 0 40px 15px;padding:10px 0;list-style-type:none}#myResume .tags{margin:5px 0 40px 15px;list-style-type:none}#myResume .tags li{border-radius:5px;float:left;margin:5px;padding:5px}#myResume section{margin-bottom:30px}#myResume section.work-experience{clear:right;margin-top:2em}#myResume section.work-experience .job{border-bottom:1px solid #3a3e40;margin:10px 0;padding:5px 20px}#myResume section.work-experience .job h4{font-size:1.2em;margin-top:0}#myResume section.work-experience ul{list-style-type:none;padding-left:0}#myResume section h2{padding-bottom:0}#myResume #about{border:1px solid #e3e3e3;margin:10px 0;padding:5px 20px}#myResume #about h4{font-size:1.2em}#myResume #about h4:first-of-type{float:right}#myResume #about ul li{margin:5px 0}#myResume #to-top{background:#2c9056;border-radius:30px;border:none;color:#fff;font-size:40px;outline:none;padding-top:10px;cursor:pointer;position:fixed;bottom:15px;right:10px;height:50px;width:50px}#myResume #grandmas{margin-top:20px;margin-bottom:200px;width:100%}
